import React, { useState } from "react";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCalendarDay,
  faFilter,
  faShip,
} from "@fortawesome/free-solid-svg-icons";

const StyledMenu = withStyles({
  paper: {
    border: "1px solid #d3d4d5",
  },
})((props) => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: "bottom",
      horizontal: "center",
    }}
    transformOrigin={{
      vertical: "top",
      horizontal: "center",
    }}
    {...props}
  />
));

const StyledMenuItem = withStyles((theme) => ({
  root: {
    "&:focus": {
      "& .MuiListItemIcon-root": {
        color: "#757575",
      },
      "& .MuiListItemText-primary": {
        color: "#454545",
      },
    },
  },
}))(MenuItem);

export default function FilterMenuButton(props) {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleMenu = (menu) => {
    const menuOption = {
      schedule: 1,
      manifest: 2,
      passenger: 3,
      ship: 4,
    };

    handleClose();

    switch (menu) {
      case menuOption.schedule:
        if (props.toggleScheduleModal) {
          props.toggleScheduleModal();
        }
        break;
      case menuOption.manifest:
        if (props.toggleNewManifestModal) {
          props.toggleNewManifestModal();
        }
        break;
      case menuOption.passenger:
        if (props.toggleNewPassengerModal) {
          props.toggleNewPassengerModal();
        }
        break;
      case menuOption.ship:
        if (props.toggleNewShip) {
          props.toggleNewShip();
        }
        break;
      default:
        break;
    }
  };

  return (
    <div className="mt-1 pt-1 ml-1 float-right">
      <Button
        aria-controls="customized-menu"
        aria-haspopup="true"
        variant="contained"
        color="primary"
        onClick={handleClick}
        className="p-1 pr-2 pl-2 menuButton"
      >
        <FontAwesomeIcon icon={faFilter} className="mr-1" /> Filter
      </Button>
      <StyledMenu
        id="customized-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <StyledMenuItem onClick={() => console.log("test")}>
          <ListItemIcon>
            <FontAwesomeIcon icon={faCalendarDay} />
          </ListItemIcon>
          <ListItemText primary="Date" />
        </StyledMenuItem>
        <StyledMenuItem onClick={() => console.log("test")}>
          <ListItemIcon>
            <FontAwesomeIcon icon={faShip} />
          </ListItemIcon>
          <ListItemText primary="Voyage" />
        </StyledMenuItem>
      </StyledMenu>
    </div>
  );
}
